.playlist-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(15, 15, 15, 0.85);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
  }
  
  .playlist-modal {
    background: #1e1e2f;
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 20px;
    padding: 2rem;
    width: 90%;
    max-width: 400px;
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.05);
    animation: slideUp 0.3s ease-out;
    color: white;
    position: relative;
  }
  
  .playlist-modal h2 {
    margin-bottom: 1rem;
    color: #ff6b81;
    font-size: 1.8rem;
    text-align: center;
  }
  
  .playlist-modal input {
    width: 100%;
    padding: 12px 16px;
    border-radius: 12px;
    border: none;
    font-size: 1rem;
    background: #2d2d40;
    color: white;
    margin-bottom: 1rem;
    outline: none;
  }
  
  .playlist-modal button[type="submit"] {
    background: linear-gradient(45deg, #ff416c, #ff4b2b);
    border: none;
    padding: 12px 20px;
    border-radius: 12px;
    color: white;
    font-weight: bold;
    font-size: 1rem;
    width: 100%;
    cursor: pointer;
    transition: 0.3s ease;
  }
  
  .playlist-modal button[type="submit"]:hover {
    transform: scale(1.05);
    box-shadow: 0 0 15px rgba(255, 75, 43, 0.5);
  }
  
  .close-btn {
    position: absolute;
    top: 14px;
    right: 18px;
    background: none;
    border: none;
    font-size: 1.4rem;
    color: #aaa;
    cursor: pointer;
    transition: 0.2s ease;
  }
  
  .close-btn:hover {
    color: #ff4b2b;
  }
  
  @keyframes slideUp {
    from {
      transform: translateY(30px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  