.cast-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2000;
  }
  
  .cast-modal-content {
    background-color: #1a1a2e;
    border-radius: 10px;
    width: 80%;
    max-width: 800px;
    max-height: 90vh;
    overflow-y: auto;
    padding: 20px;
    position: relative;
    color: white;
  }
  
  .cast-modal-header {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
  }
  
  .cast-modal-image {
    width: 200px;
    height: 300px;
    object-fit: cover;
    border-radius: 5px;
  }
  
  .cast-modal-info {
    flex: 1;
  }
  
  .cast-modal-info h2 {
    margin-top: 0;
    color: #f5c518;
  }
  
  .cast-bio {
    margin-top: 15px;
    line-height: 1.5;
  }
  
  .cast-modal-section {
    margin-top: 20px;
  }
  
  .cast-movies-grid {
    display: flex;
    gap: 15px;
    overflow-x: auto;
    padding: 10px 0;
  }
  
  .cast-movie-card {
    min-width: 120px;
    text-align: center;
  }
  
  .cast-movie-card img {
    width: 100px;
    height: 150px;
    object-fit: cover;
    border-radius: 5px;
    margin-bottom: 5px;
  }
  
  .cast-movie-card p {
    margin: 5px 0;
    font-size: 14px;
  }
  
  .close-btn {
    position: absolute;
    top: 15px;
    right: 15px;
    background: none;
    border: none;
    color: white;
    font-size: 1.5rem;
    cursor: pointer;
  }
  
  @media (max-width: 768px) {
    .cast-modal-header {
      flex-direction: column;
    }
    
    .cast-modal-image {
      width: 100%;
      height: auto;
      max-height: 400px;
    }
    
    .cast-modal-content {
      width: 95%;
    }
  }