.auth-container {
  width: 400px;
  margin-left: 425px;
  margin-top: 10px;
  padding: 161px;
  background: #0f172a; /* Deep navy blue for a modern look */
  color: white;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.4);
  justify-content: center;
}

.auth-container h2 {
  margin-bottom: 25px;
  font-size: 24px;
  font-weight: bold;
  color: #f8fafc; /* Light grayish text */
}

.auth-container input {
  width: 90%;
  padding: 12px;
  margin: 12px 0;
  border: 1px solid #334155;
  background: #1e293b;
  color: white;
  border-radius: 6px;
  font-size: 16px;
  outline: none;
  transition: all 0.3s ease;
}

.auth-container input:focus {
  border-color: #4f46e5;
  box-shadow: 0 0 8px rgba(79, 70, 229, 0.5);
}

.auth-container button {
  width: 100%;
  padding: 12px;
  background: #e11d48;
  color: white;
  font-size: 16px;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.3s ease;
  margin-top: 10px;
}

.auth-container button:hover {
  background: #be123c;
}

/* Divider */
.divider {
  margin: 20px 0;
  text-align: center;
  font-size: 14px;
  color: #94a3b8;
  position: relative;
}

.divider::before,
.divider::after {
  content: "";
  width: 40%;
  height: 1px;
  background: #334155;
  position: absolute;
  top: 50%;
}

.divider::before {
  left: 0;
}

.divider::after {
  right: 0;
}

/* Google Button Styling */
.google-btn {
  background: #4285F4;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-size: 16px;
}

.google-btn:hover {
  background: #357ae8;
}
