.movie-detail-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(10px);
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .movie-detail-modal {
    background: #101010;
    color: #fff;
    width: 90%;
    max-width: 1000px;
    border-radius: 20px;
    padding: 2rem;
    overflow-y: auto;
    max-height: 90vh;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.9);
    position: relative;
  }
  
  .close-btn {
    position: absolute;
    top: 15px;
    right: 20px;
    font-size: 1.5rem;
    background: transparent;
    border: none;
    color: white;
    cursor: pointer;
  }
  
  .modal-header {
    display: flex;
    gap: 2rem;
    align-items: flex-start;
    flex-wrap: wrap;
  }
  
  .modal-header img {
    width: 250px;
    height: auto;
    border-radius: 15px;
    object-fit: cover;
  }
  
  .modal-info {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
  }
  
  .modal-info h2 {
    font-size: 2rem;
    color: #fff;
    margin-bottom: 0.5rem;
  }
  
  .modal-info p {
    font-size: 1rem;
    line-height: 1.6;
  }
  
  .add-btn {
    background-color: #ff4c4c;
    color: white;
    padding: 0.6rem 1.2rem;
    border: none;
    border-radius: 12px;
    cursor: pointer;
    font-weight: bold;
    transition: background 0.2s ease-in-out;
    align-self: start;
  }
  
  .add-btn:hover {
    background-color: #e63636;
  }
  
  .modal-section {
    margin-top: 2rem;
  }
  
  .modal-section h3 {
    font-size: 1.4rem;
    margin-bottom: 1rem;
    color: #ffd700;
  }
  
  .cast-list {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
  }
  
  .cast-card {
    width: 100px;
    text-align: center;
  }
  
  .cast-card img {
    width: 100px;
    height: 130px;
    border-radius: 10px;
    object-fit: cover;
  }
  
  .cast-card p {
    margin-top: 0.4rem;
    font-size: 0.9rem;
  }
  
  .review {
    background-color: #1a1a1a;
    padding: 1rem;
    border-radius: 10px;
    margin-bottom: 1rem;
    border-left: 4px solid #ff4c4c;
  }
  