/* NAVBAR STYLES */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(135deg, #1a1a2e, #16213e);
  padding: 15px 30px;
  color: white;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);
}

.logo {
  font-size: 1.7rem;
  font-weight: bold;
  letter-spacing: 1px;
  transition: transform 0.3s ease-in-out;
}

.logo:hover {
  transform: scale(1.1);
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 25px;
}

.nav-links li {
  display: inline-block;
}

.nav-links a {
  text-decoration: none;
  color: white;
  font-size: 1rem;
  font-weight: 500;
  transition: color 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.nav-links a:hover {
  color: #ff4757;
  transform: scale(1.1);
}

/* USER DROPDOWN */
.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-button {
  background-color: transparent;
  color: white;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  padding: 8px 12px;
  transition: color 0.3s ease-in-out;
}

.dropdown-button:hover {
  color: #ffcc00;
}

/* Dropdown Menu */
.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #16213e;
  border-radius: 8px;
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.4);
  list-style: none;
  padding: 10px 0;
  min-width: 180px;
  display: flex;
  flex-direction: column;
  opacity: 0;
  transform: translateY(-10px);
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.dropdown:hover .dropdown-menu {
  opacity: 1;
  transform: translateY(0);
  visibility: visible;
}

.dropdown-menu li {
  padding: 12px;
  text-align: left;
  cursor: pointer;
  transition: background 0.3s ease-in-out;
}

.dropdown-menu li:hover {
  background-color: #ff4757;
}

.dropdown-menu a {
  text-decoration: none;
  color: white;
  display: block;
}
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #101010;
  padding: 1rem 2rem;
}

.navbar-search {
  flex: 1;
  margin-left: 2rem;
}
