/* BODY BACKGROUND */
body {
  background: linear-gradient(135deg, #141e30, #243b55);
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  color: #fff;
}

/* MAIN CONTAINER */
.home-container {
  max-width: 1400px;
  margin: auto;
  padding: 50px 20px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* TITLE + SUBTITLE */
.home-title {
  font-size: 2.5rem;
  font-weight: 800;
  color: #ff6b81;
  margin-bottom: 10px;
  text-align: center;
}

.subtitle {
  font-size: 1.2rem;
  color: #ccc;
  margin-bottom: 30px;
  text-align: center;
  max-width: 700px;
}

/* SEARCH BOX */
.search-box {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 40px;
  flex-wrap: wrap;
  justify-content: center;
}

.search-input {
  padding: 14px 24px;
  width: 320px;
  border-radius: 30px;
  border: none;
  outline: none;
  font-size: 1rem;
  background: #1f1f2e;
  color: white;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.search-input::placeholder {
  color: #aaa;
}

.search-button {
  background: linear-gradient(45deg, #ff416c, #ff4b2b);
  border: none;
  padding: 14px 30px;
  border-radius: 30px;
  color: white;
  font-weight: bold;
  font-size: 1rem;
  cursor: pointer;
  transition: 0.3s ease;
  box-shadow: 0 0 15px rgba(255, 75, 43, 0.5);
}

.search-button:hover {
  transform: scale(1.05);
}

/* MOVIE GRID */
.movies-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 25px;
  width: 100%;
}

/* MOVIE CARD */
.movie-card {
  background: #1e1e2f;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.05);
  transition: transform 0.3s ease-in-out;
  cursor: pointer;
}

.movie-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 0 25px rgba(255, 204, 0, 0.2);
}

.movie-poster {
  width: 100%;
  height: 320px;
  object-fit: cover;
}

/* MOVIE TITLE */
.movie-title {
  font-size: 1.1rem;
  font-weight: 600;
  text-align: center;
  color: #ffd369;
  margin: 10px 0 15px;
}

/* ERROR & LOADING */
.error-text,
.loading-text {
  font-size: 1.1rem;
  margin-top: 20px;
  color: #ff6b81;
  font-weight: 600;
  text-align: center;
}

/* MODAL OVERLAY */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(15, 15, 15, 0.85);
  width: 100%;
  height: 100%;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* MODAL CONTENT */
.modal-content {
  background: #1e1e2f;
  padding: 30px;
  border-radius: 20px;
  width: 90%;
  max-width: 950px;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.5);
  color: white;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.modal-body {
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
}

.modal-poster {
  width: 280px;
  border-radius: 12px;
}

.modal-info {
  flex: 1;
  text-align: left;
}

.modal-title {
  font-size: 2.3rem;
  color: #ff6b81;
  margin-bottom: 15px;
}

.modal-overview {
  font-size: 1rem;
  color: #ddd;
  margin-bottom: 10px;
}

.modal-cast {
  font-size: 1rem;
  color: #bbb;
}

/* CLOSE BUTTON */
.close-button {
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 1.8rem;
  color: white;
  background: none;
  border: none;
  cursor: pointer;
  transition: 0.2s ease;
}

.close-button:hover {
  color: #ff4b2b;
}
