/* AddMovieModal.css */

.add-movie-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.add-movie-modal {
  background: #f9f9fb;
  border-radius: 20px;
  width: 100%;
  max-width: 450px;
  padding: 30px;
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
  position: relative;
  animation: scaleFade 0.25s ease-in-out;
}

@keyframes scaleFade {
  0% {
    opacity: 0;
    transform: scale(0.95);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.close-btn {
  position: absolute;
  top: 14px;
  right: 18px;
  background: transparent;
  border: none;
  font-size: 20px;
  color: #333;
  cursor: pointer;
  transition: color 0.2s;
}

.close-btn:hover {
  color: #000;
}

h2 {
  font-size: 1.6rem;
  color: #222;
  margin-bottom: 18px;
  text-align: center;
  font-weight: 600;
}

form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.select-wrapper {
  font-family: inherit;
}

.react-select__control {
  border-radius: 12px;
  border: 1px solid #ccc;
  padding: 2px;
  box-shadow: none;
  transition: border-color 0.2s;
}

.react-select__control--is-focused {
  border-color: #007bff;
}

.react-select__menu {
  border-radius: 12px;
  z-index: 9999;
  font-size: 14px;
}

.react-select__option--is-focused {
  background-color: #e0f0ff;
}

button[type="submit"] {
  background: #007bff;
  color: white;
  padding: 10px 16px;
  font-size: 15px;
  font-weight: 600;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
}

button[type="submit"]:hover {
  background-color: #0056b3;
}
.success-msg {
  color: green;
  font-weight: bold;
  margin-top: 10px;
  text-align: center;
}
