/* Profile.css - Stylish UI */
.profile-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(to right, #1a1a2e, #16213e);
  }
  
  .profile-card {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    padding: 30px;
    border-radius: 15px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    text-align: center;
    width: 350px;
  }
  
  .profile-card h2 {
    color: #fff;
    margin-bottom: 20px;
    font-size: 1.8rem;
  }
  
  .profile-card input {
    width: 100%;
    padding: 12px;
    margin: 10px 0;
    border: none;
    border-radius: 8px;
    font-size: 1rem;
    background: rgba(255, 255, 255, 0.2);
    color: #fff;
    outline: none;
    text-align: center;
  }
  
  .profile-card input::placeholder {
    color: rgba(255, 255, 255, 0.7);
  }
  
  .update-btn {
    background: #ff4757;
    color: white;
    font-size: 1.1rem;
    padding: 12px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background 0.3s ease-in-out;
    width: 100%;
  }
  
  .update-btn:hover {
    background: #e84142;
  }
  