/* src/pages/Library.css */
.library-container {
    padding: 60px 20px;
    max-width: 1200px;
    margin: auto;
    color: #fff;
  }
  
  .library-title {
    text-align: center;
    font-size: 2.5rem;
    color: #ff6b81;
    margin-bottom: 40px;
  }
  
  .playlist-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
    gap: 25px;
  }
  
  .playlist-card {
    background: #1f1f2e;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 0 12px rgba(255, 255, 255, 0.05);
    text-align: center;
    transition: transform 0.3s ease;
  }
  
  .playlist-card:hover {
    transform: translateY(-8px);
    box-shadow: 0 0 20px rgba(255, 107, 129, 0.3);
  }
  