.contact-container {
    max-width: 700px;
    margin: 4rem auto;
    background: rgba(255, 255, 255, 0.05);
    padding: 3rem 2rem;
    border-radius: 1.5rem;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.15);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
    color: #e2e8f0;
  }
  
  .contact-title {
    font-size: 2.5rem;
    font-weight: bold;
    color: #f1f5f9;
    text-align: center;
    margin-bottom: 0.5rem;
  }
  
  .contact-subtitle {
    text-align: center;
    color: #94a3b8;
    margin-bottom: 2rem;
  }
  
  .contact-form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .contact-input,
  .contact-textarea {
    padding: 1rem;
    font-size: 1rem;
    border-radius: 1rem;
    border: none;
    outline: none;
    background: #f1f5f9;
    color: #0f172a;
    transition: box-shadow 0.2s ease;
  }
  
  .contact-input:focus,
  .contact-textarea:focus {
    box-shadow: 0 0 0 2px #3b82f6;
  }
  
  .contact-submit-btn {
    padding: 0.9rem;
    font-size: 1.1rem;
    background-color: #3b82f6;
    color: white;
    border: none;
    border-radius: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .contact-submit-btn:hover {
    background-color: #2563eb;
  }
  