/* Movie Modal Overlay */
.movie-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7); /* Dark overlay for contrast */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    transition: all 0.3s ease-in-out;
  }
  
  /* Modal Content */
  .movie-modal-content {
    background: #2c2c2c; /* Dark background for modal content */
    color: #f1f1f1; /* Light text color for contrast */
    padding: 30px;
    max-height: 90vh;
    overflow-y: auto;
    border-radius: 12px;
    width: 80%;
    position: relative;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease-in-out;
  }
  
  /* Close Button */
  .close-btn {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 24px;
    color: #f1f1f1; /* Light color for visibility */
    background: none;
    border: none;
    cursor: pointer;
    transition: color 0.3s ease;
  }
  
  .close-btn:hover {
    color: #ff6f61; /* Hover color for the close button */
  }
  
  /* Modal Header */
  .movie-modal-content h2 {
    font-size: 2rem;
    margin-bottom: 15px;
    color: #ff6f61; /* Highlight color for the title */
  }
  
  .movie-modal-content p {
    font-size: 1rem;
    line-height: 1.5;
    margin-bottom: 15px;
    color: #d1d1d1; /* Slightly lighter text for paragraph content */
  }
  
  /* Cast Section */
  .movie-modal-content h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
    color: #ff6f61; /* Highlight color for section titles */
  }
  
  /* Cast List */
  .movie-modal-content ul {
    list-style-type: none;
    padding: 0;
  }
  
  .movie-modal-content li {
    font-size: 1rem;
    margin-bottom: 8px;
    color: #ddd; /* Light gray for cast text */
  }
  
  /* Trailers Section */
  .movie-modal-content iframe {
    width: 100%;
    height: 315px;
    border-radius: 8px;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.15);
    margin-bottom: 20px;
  }
  
  /* Review Section */
  .movie-modal-content .review {
    margin-bottom: 15px;
    font-size: 1rem;
    color: #ccc; /* Slightly lighter color for reviews */
  }
  
  .movie-modal-content .review p {
    margin: 0;
  }
  
  /* Review Author */
  .movie-modal-content .review-author {
    font-weight: bold;
    color: #ff6f61; /* Highlighted author name color */
  }
  
  /* Smooth Scroll for Modal */
  .movie-modal-content::-webkit-scrollbar {
    width: 8px;
  }
  
  .movie-modal-content::-webkit-scrollbar-thumb {
    background-color: rgba(255, 111, 97, 0.5);
    border-radius: 4px;
  }
  
  .movie-modal-content::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.2);
  }
  
  /* Mobile Responsiveness */
  @media (max-width: 768px) {
    .movie-modal-content {
      width: 90%;
      padding: 20px;
    }
  
    .close-btn {
      top: 15px;
      right: 15px;
    }
  
    .movie-modal-content h2 {
      font-size: 1.8rem;
    }
  
    .movie-modal-content h3 {
      font-size: 1.3rem;
    }
  }
  